<template>
    <div>
        <div v-html="formHTML"></div>
        <el-table ref="multipleTable" :data="tableDate" tooltip-effect="dark" style="width: 100%;margin-bottom:10px" @selection-change="handleSelectionChange">
            <el-table-column align="center" label="" type="index" width="50">
            </el-table-column>
            <el-table-column prop="createDate" align="center" label="" width="160">
            </el-table-column>
            <el-table-column prop="receiveUsername" align="center" label="" width="100">
            </el-table-column>
            <el-table-column prop="receivePhone" align="center" label="" width="110">
            </el-table-column>
            <el-table-column prop="totalPrice" align="center" label="" width="80">
            </el-table-column>
            <el-table-column prop="totalNum" align="center" label="" width="80">
            </el-table-column>
            <el-table-column prop="receiveAddress" align="center" label="" show-overflow-tooltip>
            </el-table-column>
            <el-table-column align="center" label="" width="100">
                <!-- 1. 2. 3. 4. 5.       -->
                <template slot-scope="scope">
                    <el-tag size="small">
                        {{ scope.row.status==1?'':scope.row.status==2?'': scope.row.status==3?'':scope.row.status==4?'':scope.row.status==5?'':''}}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column align="center" label="" width="180" v-if="activeName=='first' || activeName=='second'|| activeName=='fourth'">
                <template slot-scope="scope">
                    <el-button v-show="scope.row.status==1" type="text" size="small" @click="payOrder(scope.row)"></el-button>
                    <el-button v-show="scope.row.status==1" style="color:red" type="text" size="small" @click="closeOrder(scope.row.orderId)"></el-button>
                    <el-button v-show="scope.row.status==3" type="text" size="small" @click="getOrder(scope.row.orderId)"></el-button>
                    <el-button type="text" size="small" @click="detailOrder(scope.row.orderId)"></el-button>

                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" :visible.sync="dialogTableVisible" width="50%">
            <div class="info">
                <div>{{ orderDetail.receiveUsername }}</div>
                <div>{{ orderDetail.receivePhone }}</div>
                <div>{{ orderDetail.receiveAddress }}</div>
                <div>{{ orderDetail.createDate }}</div>
                <div>{{ orderDetail.status==1?'':orderDetail.status==2?'': orderDetail.status==3?'':orderDetail.status==4?'':orderDetail.status==5?'':''}}</div>
            </div>
            <el-table :data="orderDetail.orderItems?orderDetail.orderItems:[]">
                <el-table-column align="center" label="" type="index" width="60">
                </el-table-column>
                <el-table-column align="center" label="" width="100">
                    <template slot-scope="scope">
                        <img :src="scope.row.cover" alt="" height="50px">
                    </template>
                </el-table-column>
                <el-table-column prop="name" align="center" label="" min-width="100">
                    <template slot-scope="scope">
                        {{ scope.row.name}}
                    </template>
                </el-table-column>
                <el-table-column prop="price" align="center" label="" min-width="80">
                    <template slot-scope="scope">
                        {{ scope.row.price}}
                    </template>
                </el-table-column>
                <el-table-column prop="num" align="center" label="" min-width="110">
                </el-table-column>
                <el-table-column prop="totalPrice" align="center" label="" min-width="80">
                </el-table-column>
            </el-table>
        </el-dialog>

    </div>
</template>

<script>
import { put, get } from "@/utils/request";
import axios from "axios";
export default {
    props: ["tableDate", "activeName"],
    data() {
        return {
            formHTML: "",
            dialogTableVisible: false,
            orderDetail: {},
        };
    },
    created() {},
    mounted() {
        //
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        selectable(row, index) {
            console.log(row);
            if (row.status == 1) {
                return true;
            }
        },
        // 
        closeOrder(id) {
            put(`/order/close/${id}`).then((res) => {
                console.log(res);
                if (res && res.message == "ok") {
                    this.$notify({
                        title: "",
                        message: "",
                        type: "success",
                    });
                    this.$emit("reLoad", this.activeName);
                } else {
                    this.$notify.error({
                        title: "",
                        message: "",
                    });
                }
            });
        },
        // 
        payOrder(val) {
            put("/order/pay", {
                orderId: val.orderId,
                pathUrl: "http://localhost:8080/#/paySuccess",
                totalPrice: val.totalPrice,
            }).then((res) => {
                this.formHTML = res.data;
                this.$nextTick(() => {
                    document.forms[0].submit();
                });
            });
        },
        // 
        getOrder(orderId) {
            put(`/order/complete/${orderId}`)
                .then((res) => {
                    console.log("", res);
                    if (res && res.message == "ok") {
                        this.$message({
                            message: "",
                            type: "success",
                        });
                        this.$emit("reLoad", this.activeName);
                    }
                })
                .catch(() => {
                    this.$message.error("");
                });
        },
        // 
        detailOrder(orderId) {
            get(`/order/${orderId}`).then((res) => {
                if (res && res.data) {
                    console.log(9999, res.data);

                    this.orderDetail = res.data;
                    this.dialogTableVisible = true;
                }
            });
        },
    },
};
</script>
<style lang="scss" scoped>
.info {
    border: 1px solid #ccc;
    padding: 10px 20px;
    margin-bottom: 10px;
    div {
        line-height: 30px;
    }
}
</style>
