import Vue from 'vue'
import VueRouter from 'vue-router'
import { getToken } from '@/utils/auth'
import store from '@/store/index'

import Home from '@/views/home/index'    // 
import Register from '@/views/register/index'    // 
import Login from '@/views/login/index'     // 
import UserInfo from '@/views/userInfo/index'     // 
import Collect from '@/views/collect/index'  // 
import Order from '@/views/order/index'  // 
import PaySuccess from '@/views/order/paySuccess'  //
import Detail from '@/views/Components/Container/Detail'  // 
import ShopCar from '@/views/shopCar/index'  // 
import Settlement from '@/views/shopCar/settlement'  // 




Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        redirect: '/home',
    },
    {
        path: '/home',  //
        name: 'Home',
        component: Home,
        meta: { auth: false }
    },
    {
        path: '/userInfo',   // 
        name: 'UserInfo',
        component: UserInfo,
        meta: { auth: true }
    },
    {
        path: '/collect',   // 
        name: 'Collect',
        component: Collect,
        meta: { auth: true }
    },
    {
        path: '/order',   // 
        name: 'Order',
        component: Order,
        meta: { auth: true }
    },
    {
        path: '/paySuccess',   // 
        name: 'PaySuccess',
        component: PaySuccess,
        meta: { auth: true }
    },
    {
        path: '/detail',   // 
        name: 'Detail',
        component: Detail,
        meta: { auth: true }
    },
    {
        path: '/shopCar',   // 
        name: 'ShopCar',
        component: ShopCar,
        meta: { auth: true }
    },
    {
        path: '/settlement',   // 
        name: 'Settlement',
        component: Settlement,
        meta: { auth: true }
    },
    {
        path: '/login',   // 
        name: 'Login',
        component: Login,
        meta: { auth: false }

    },
    {
        path: '/register',    // 
        name: 'Register',
        component: Register,
        meta: { auth: false }
    },
    {
        path: '/404',
        component: () => import('@/views/404'),
        meta: { auth: false }
    },
    { path: '*', redirect: '/404', hidden: true }
]

const router = new VueRouter({
    scrollBehavior: () => ({ y: 0 }),
    routes
})

const whiteList = ['/login', '/home', '/register']
const notHeadFootList = ['/login', '/register']   //
router.beforeEach(async (to, from, next) => {
    if (notHeadFootList.indexOf(to.path) !== -1) {
        store.dispatch("changeShowHeadFoot", false)
    } else {
        store.dispatch("changeShowHeadFoot", true)
    }
    const hasToken = getToken()
    if (hasToken) {

        if (to.path === '/login') {
            next({ path: '/home' })
        } else {
            const hasUserInfo = store.state.userInfo
            if (hasUserInfo) {
                next()
            } else {
                try {
                    await store.dispatch('getInfo')
                    next()
                } catch (error) {
                    await store.dispatch('resetToken')
                    next('/login')
                }
            }
        }
    } else {

        if (whiteList.indexOf(to.path) !== -1) {
            next()
        } else {
            next('/login')
        }
    }
})



export function resetRouter() {
    const newRouter = createRouter()
    router.matcher = newRouter.matcher // reset router
}

export default router
