<template>
    <div id="top" v-if="isShow">
        <div class="rightBtn">
            <div v-if="hasToken">
                <span v-show="$route.path !== '/home'" @click="$router.push('/home')"></span>
                <span @click="toUserInfo"></span>
                <span @click="toMyOrder"></span>
                <span @click="toMyCollect"></span>
                <span @click="toMyShopCar"></span>
                <span> {{ username }}</span>
                <span @click="toOutLogin"></span>
            </div>
            <div v-else>
                <span @click="toLogin"></span>
                <span @click="toRegister"></span>
            </div>
        </div>
    </div>
</template>

<script>
import { getToken, removeToken } from "@/utils/auth";
export default {
    data() {
        return {
            username: this.$store.state.userInfo
                ? this.$store.state.userInfo.username
                : "XXXX",
            hasToken: getToken(),
            isShow: this.$store.state.isShowHeadFoot,
        };
    },
    created() {},
    methods: {
        // 
        toUserInfo() {
            this.$router.push("/userInfo");
        },
        //  
        toMyOrder() {
            this.$router.push("/order");
        },
        //  
        toMyCollect() {
            this.$router.push("/collect");
        },
        // 
        toOutLogin() {
            // token
            removeToken();
            this.$router.push("/login");
        },
        // 
        toLogin() {
            this.$router.push("/login");
        },
        // 
        toRegister() {
            this.$router.push("/register");
        },
        toMyShopCar() {
            this.$router.push("/shopCar");
        },
    },
};
</script>
<style lang="scss" scoped>
#top {
    height: 40px;
    background-color: #b11e22;

    .rightBtn {
        margin-right: 10%;
        float: right;
        line-height: 40px;
        color: #fff;
        span {
            margin-left: 20px;
            &:hover {
                cursor: pointer;
            }
        }
    }
}
</style>
