<template>
    <div>
        <Header />
        <div class="bookInfo" v-loading="loading">
            <div class="left-box">
                <img :src="bookInfo.cover" alt="" height="260">
            </div>
            <div class="right-box">
                <h2>{{ bookInfo.name }}</h2>
                <div style="border-bottom: 1px solid #f5f5f5;">{{ bookInfo.description }}</div>
                <div>
                    <span>{{ bookInfo.author }}</span>
                    <span style="margin:20px">{{ bookInfo.publisher }}</span>
                </div>
                <div class="price"> <span>{{ bookInfo.price }}</span> </div>
                <div>
                    
                    <el-input-number step-strictly size="medium" style="width:140px" v-model="number" @change="handleChange" :min="1" :max="bookInfo.storeNum">
                    </el-input-number>
                    {{ bookInfo.storeNum }}
                </div>
                <div>
                    <el-select v-model="addressVal" placeholder="" style="width:300px">
                        <el-option v-for="item in addressList" :key="item.id" :label="item.urbanAddr + item.detailAddr" :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="btn">
                    <el-button-group>
                        <!-- <el-button size="medium" class="buyBtn" @click="handleBuyNew"></el-button> -->
                        <el-button size="medium" class="addBtn" @click="addShopCar"></el-button>
                    </el-button-group>
                    <div class="collect">
                        <div v-if="bookInfo.collect" @click="handleDelCollect(bookInfo.id)">
                            <i class="el-icon-star-on"></i> 
                        </div>
                        <div v-else @click="handleCollect(bookInfo.id)" style="cursor: pointer;">
                            <i class="el-icon-star-off"></i> 
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Footer />
    </div>

</template>

<script>
import Header from "@/views/Components/Header/index.vue";
import Footer from "@/views/Components/Footer/index.vue";

import { get, postJSON, delJSON, put } from "@/utils/request";
export default {
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            bookId: "",
            bookInfo: {},
            number: "",
            loading: false,
            addressList: [],
            addressVal: "",
        };
    },
    created() {
        this.bookId = this.$route.query.id;
        this.getBookInfo();
        this.getAddressList();
    },
    methods: {
        // 
        getBookInfo() {
            this.bookInfo = [];
            this.loading = true;
            get(`/book/${this.bookId}`).then((res) => {
                if (res && res.data) {
                    this.bookInfo = res.data;
                    this.loading = false;
                }
            });
        },
        // 
        getAddressList() {
            this.addressList = [];
            this.addressVal = "";
            get("/address").then((res) => {
                if (res && res.data) {
                    console.log("", res.data);
                    this.addressList = res.data;
                    this.addressVal = res.data.find((item) => {
                        return item.defaultValue;
                    }).id;
                }
            });
        },
        handleChange() {},
        // 
        handleCollect(id) {
            postJSON(`/collect/${id}`)
                .then((res) => {
                    if (res && res.message == "ok") {
                        this.$message({
                            message: "",
                            type: "success",
                        });
                        this.getBookInfo();
                    }
                })
                .catch((err) => {
                    this.$message.error("");
                });
        },

        // // 
        // handleDelCollect(id) {
        //     let ids = [];
        //     ids[0] = id;
        //     delJSON("/collect/", ids).then((res) => {
        //         console.log(res);
        //         if (res && res.message == "ok") {
        //             this.$message({
        //                 message: "",
        //                 type: "success",
        //             });
        //             this.getBookInfo();
        //         }
        //     });
        // },

        // 
        handleBuyNew() {},
        // 
        addShopCar() {
            put(`/shopCar/${this.bookId}`).then((res) => {
                if (res && res.message == "ok") {
                    this.$message({
                        message: "",
                        type: "success",
                    });
                }
            });
        },
    },
};
</script>
